<template>
<div id="display">
  <div id="border-box">
    <svg 
    viewBox="0 0 710 710" 
    version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"
    fill="none">
      <rect id="border" stroke-width="1.5" x="0.6" y="0.6" width="708.8" height="708.8"></rect>
    </svg>
  </div>

  <div id="content">
    <PathView
    v-if="pathFile0.length !== 0" 
    :pathData="pathFile0"
    />
    <PathView
    v-if="pathFile1.length !== 0" 
    :pathData="pathFile1"
    />
  </div>





</div>
</template>



<script>
import { mapState, mapGetters, mapMutations } from 'vuex';
import anime from 'animejs';
const { keys, Timeline } = require('../../assets/javascripts/circleAnime');
const { randomInt } = require('../../assets/javascripts/uiAction');
import PathView from './display/PathView';

const { signFiles } = require('../../test/test')

export default {
  name: "Display",
  components: { PathView,  },
  data() { return {
    BorderIn: null, BorderOut: null,
    pathFile0: [], pathFile1: [],
    decider: true,
    pending: []








  }},
  computed: {
    ...mapState([
      'renderStatus'
    ]),
    ...mapGetters([
      'SIGNS',
      'SIGNS_COUNT'
    ]),


  },
  watch: {
    renderStatus(nu, old){
      if(nu === 1){//___when mounted
        this.aIn();
        this.pending = this.pull(this.SIGNS, this.SIGNS_COUNT);

      }else if(nu === 2){//___when rendered
        this.aOut();

      }else if(nu ===3){//___when destroyed
        if(this.decider){
          this.pathFile0 = [];
        }else{
          this.pathFile1 = [];
        }
        setTimeout(this.MOUNT, 500, this.pending);
        this.$store.state.renderStatus = 0;
      }
        return old
    }
  },
  methods: {
    ...mapMutations([]),
    aIn(){
      this.BorderIn = null;
      this.BorderOut = null;
      this.BorderIn =  Timeline(anime)
      .add({
        targets: '#border',
        stroke: [
          keys('rgba(255, 255, 255, 0)', 0, 1, 0, "easeOutExpo"),
          keys('rgba(255, 255, 255, 1)', 0, 800, 1000, "easeOutExpo")
        ],
      });
      this.BorderIn.play();
    },
    aOut(){
      this.BorderOut = Timeline(anime)
      .add({
      targets: '#border',
      strokeDashoffset: [{
          value: [0, anime.setDashoffset], 
          delay: 1300,
          duration: 3200, // HOW LONG WOULD BE THE PATH DISPLAYED
          endDelay: 0,
          direction: 'normal',
          easing: "easeInOutQuart",
        }],
      })
      .add({
        targets: '#border',
        stroke: [
          keys('rgba(255, 255, 255, 0)', 0, 1, 0, "easeOutExpo")
        ],
      })
      .add({
      targets: '#border',
      strokeDashoffset: [{
          value: [anime.setDashoffset, 0], 
          delay: 0,
          duration: 1,
          endDelay: 0,
          direction: 'normal',
          easing: "easeInOutQuart",
        }],
      });
      this.BorderOut.finished.then(() => {
        this.$store.state.renderStatus += 1;
      });
      this.BorderOut.play();
    },
    pull(arr, count){
      const num = randomInt(0, count);
      return arr[num]
    },
    MOUNT(pathData){
      if(this.decider){
        this.pathFile0 = pathData;
        console.log('mounted to:', 0);
      }else{
        this.pathFile1 = pathData;
        console.log('mounted to:', 1);
      }
      this.decider = !this.decider;
    }





  },
  created() {
    
  },
  mounted() {
    if(this.$store.state.test.server.signLoad){
      this.$store.state.signsArr = signFiles;
    }
    this.pending = this.pull(this.SIGNS, this.SIGNS_COUNT);
    setTimeout(this.MOUNT, 1500, this.pending);
  },
  beforeUpdate() {
    
  },
  beforeCreate() {
    
  },
}
</script>



<style lang="scss" scoped> 
#display{
  position: absolute;
  top: 1%; left: 1%;
  width: 98%; height: 98%;
}
#border-box{
  position: absolute;
  pointer-events: none;
  width: 100%; height: 100%;
  // stroke: rgba(255, 255, 255, 0);
}




</style>